import React from "react"
import "./About.css"
import styled,{css} from "styled-components"
import {Text,ColumnWrap} from "./Utils"
import myBox from "../image/box.svg"

//대외활동 그래픽
const Circle = styled.div`
    border:3px solid #ffffff;
    border-radius:50%;
    background:#ffc3c0;
    width: 1.4rem;
    height: 1.4rem;
    margin-bottom:10px;
    position:relative;
    box-sizing:border-box;
`;
const Line = styled.div`
    border: 2px solid  #ffc3c0;
    border-right-width:0px;
    border-left-width:0px;
    width: 60rem; 
    position:relative;
    @media (max-width: 639px) {
        width:80rem;
    }
`;// % 안먹힘(부모 width값을 fit-content로 해서(자식이 absolute))
//원+시점별 활동 세로로 묶음
const Wrap = styled.div`
    ${ColumnWrap}
    position:absolute;
    top:-0.6rem;
    transform:translateX(${props=>props.posX}rem);
    line-height:2.4rem;
    @media (max-width: 639px) {
        transform:translateX(${props=>props.posX*1.8}rem);
    }
`;
//Wrap+라인
const Graphic = styled.div`
    width:fit-content;
    transform:translate(18rem, 30rem);
    @media (max-width: 1023px) {

    }
    @media (max-width: 767px) {
    
    }
    @media (max-width: 639px) {
        transform:unset;
        margin:3rem auto;
    }
`;

const About = () => {

    return (
        <div className="About" id="About">
            <div className="box box-flex">
                <div className="profile">
                    <div className="profile__photo">

                    </div>
                </div>
                <div>
                    <div className="text-box1">
                        <h2>처음 뵙겠습니다! 👋</h2>
                        <Text size="1.4" style={{lineHeight:"2.6rem"}}>
                            초보 웹개발자 양승주입니다.<br/>
                            혼자 이것저것 만들며 웹서비스 전반에 대해 공부하고 있어요<br/>
                            <br/>
                            시작은 독박으로 웹개발 조별과제를 맡게 된 것이었기에
                            힘들고 어려웠지만 <br/>스스로 결과물을 만들고 나서의 성취감은
                            저를 계속 나아갈 수 있게 해주는 동기가 되었습니다.
                            <br/><br/>
                            제 장점은 어떤 상황에서도 포기하지 않고 최선을 다해 주어진 일을 완수한다는 점입니다.<br/>
                            특히 혼자가 아닌 동료들과 함께 하는 상황에서는 더 큰 시너지를 발휘합니다.<br/>
                            수많은 장애물이 앞을 가로막을 때에도 끈기와 동료로서의 책임감을 가지고 꿋꿋하게 완주해냅니다.
                            <br/><br/>
                            아직 서툴고 부족한 점도 많지만 스스로 채워나갈 자신이 있습니다.<br/>
                            제가 더 나은 개발자가 되는 걸 지켜봐 주세요 :-) 
                        </Text>
                    </div>
                </div>
            </div>
            <div className="box box2">
                {/* <img src={wave} alt='wave' /> */}
                <img src={myBox} alt='image' />
                <div className="text-box2">
                    <Text size="1.2" bold>저는 넘치는 열정과 <br/>무한한 잠재력을 가지고 있어요:)</Text>
                </div>
                <div className="text-box3">
                    <div>
                        <p>이름</p>
                        <p>나이</p>
                        <p>최종학력</p>
                        <p>취미</p>
                        <p>대외활동</p>
                    </div>
                    <div>
                        <p>양승주</p>
                        <p>95.08.03</p>
                        <p>세종대학교 디지털콘텐츠(현 소프트웨어)학과</p>
                        <p>소설/게임/넷플릭스보기</p>
                        <p></p>
                    </div>
                </div>
                <Graphic>
                        <Line>

                            <Wrap>
                                <Circle/>
                                <Text size="1.4" bold>코딩클럽</Text>
                                <Text size="1">2017.10.01~2017.12.31</Text>
                            </Wrap>
                            <Wrap posX="15">
                                <Circle/>
                                <Text size="1.6" bold>무중력지대양천</Text>
                                <Text size="1.2">홍보•기획 매니저</Text>
                                <Text size="1">2018.02.01~2018.8.31</Text>
                                <Text size="1">2019.01.14~2020.01.31</Text>
                            </Wrap>
                            <Wrap posX="30">
                                <Circle/>
                                <Text size="1.4" bold>4차 산업혁명 동아리 TAVE</Text>
                                <Text size="1.2">전반기 스터디3등/후반기 1등</Text>
                                <Text size="1">2019.01.14~2020.01.31</Text>
                            </Wrap>
                        </Line>
                </Graphic>
                        
            </div>
        </div>
    );
}

export default About;