import { render } from "@testing-library/react";
import React,{useState} from "react";
import styled, {css} from "styled-components";
import banner from "../image/banner.svg"
import Modal from '../container/Modal';
import {TextCss,ContainerCSS} from '../components/Utils';
import pro1 from "../image/project1.svg";
import construction from "../image/construction.svg";

// 페이지 전체 감싸줄 컨테이너
const Container = styled.div`
// border:1px solid blue;
    ${ContainerCSS}
    height:auto;
`;


//헤더 부분
const Header = styled.div`
    width:100%;
    min-height:30rem;
    // border:4px solid red;
`;
//최근 프로젝트 부분
const RecentProject = styled.div`
    // border:3px solid orange;
    padding-left:18rem;

    font-weight:bold;
    & h2{
        font-size:3.8rem;
        // border:1px solid red;
    }
    & > div{
        display:flex;
        align-items:start;
        flex-wrap:wrap;
    }
      
    @media (max-width: 639px) {
        padding:unset;
        margin:0 auto;
    }
`;
//작업물 부분
const Others = styled.div`
    min-height:20rem;
    margin:0 10rem;
    
    
    @media (max-width: 767px) {
        margin:0 auto;
    }
    @media (max-width: 639px) {
        margin:0 auto;
    }

`; 


//배너 이미지
const BannerImg = styled.div`
    width: 100%;
    height: 30rem;
    
    background-image: url(${banner});
    background-color: black; 
    background-repeat: no-repeat;
    background-size:cover;
    position:absolute;
`;

//배너 위 글씨
const HeaderTitle = styled.h2`
    font-size:7.8rem;
    color:white;
    margin:0;
    position:absolute;
    transform:translate(15rem,7rem);
    & p{
        font-size:1.8rem;
        font-weight:normal;
        margin:0;
        line-height:2.8rem;
    }
    & div{
        width:16%;
        border-top:4px solid white;
        margin:2rem 0 1rem 0;
    }
    `;


//배너 전체 컨테이너
const BannerBox=styled.div`
    position:relative;
    // border:2px solid blue;
`;




//프로젝트 썸네일
const Thumbnail = styled.div`
    width:${props=>props.width||'40rem'};
    height:${props=>props.height||'30rem'};
    // border:2px solid black;
`;

//프로젝트 제목
const Title = styled.div`
    font-size:3rem;
    margin-bottom:2rem;
`;
//중간 제목
const MidTitle =styled.div`
    font-size:1.8rem;
    margin-bottom:0.5rem;
`; 
//일반 텍스트
const Basic = css`
    font-size:1.4rem;
    line-height:3rem;
`;

const Text = styled.div`
    ${Basic}
    margin-bottom:2rem;
    & p{
        font-size:1.8rem;
        font-weight:800;
    }
`;
const Empha=styled.div`
    background:#ffaaa6;
    color:#ffffff;
    padding:0 1.1rem;
    // text-align:-webkit-center;
    `;
//텍스트 박스
const TextBox = styled.div`
    display:flex;
    flex-direction:column;
    // border:2px solid black;
`;


/* 기타 작업물 */
//그리드
const Grid = styled.div`
    display: grid;
    grid-template-columns: repeat(4, 1fr); //각 행(세로줄)의 길이
    //grid-template-rows: 400rem/*각 열(가로줄)의 길이*/
    gap: 1rem; /* 자식요소간의 간격 */
    box-sizing: border-box;
    padding: 5rem 0;
    // border:5px solid red;
    

    min-height:60rem; //초반 높이설정 width/height 100% 
    & div{
        // border:1px solid green;
        // background:yellow;
        display:flex;
        align-items:center;
        justify-content:center;
    }
    
    `;
//그리드 박스
const GridBox = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
`;
//해시태그 글씨
const HashTag = styled.div`
    ${Basic}
    font-weight:800;
`;


//아웃라인 글씨
const LineText = styled.div`
    ${Basic}
    font-size:4.5rem;
    font-weight:800;
    color:white;
    text-shadow: 1.5px 0px #ffaaa6, 0px 1.5px #ffaaa6, -1.5px 0px #ffaaa6, 0px -1.5px #ffaaa6; 
    -webkit-text-size-adjust: 100%;
        -ms-text-size-adjust: 100%;
        color:#ffffff;
    position:relative;
`;
//원 콘텐츠
const Circle = styled.div`
    width:5rem;
    height:5rem;
    border-radius:50%;
    background:#fee4a6;
    position:absolute;
    transform:translate(-4rem, 2rem)
`;
//일반 글씨
const TextBase=styled.div`
    ${TextCss}
    color:rgb(249, 242, 242);
    text-shadow: 1.5px 0px 1px rgba(57, 57, 61, 0.3), 0px 1.5px 1px rgba(57, 57, 61, 0.3), -1.5px 0px 1px rgba(57, 57, 61, 0.3),0px -1.5px 1px rgba(57, 57, 61, 0.3);
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    // border:1px solid red;
`;
const VerticalText=styled.div`
    writing-mode:vertical-lr;
    position:relative;

`;

const Project=()=>{


        const [modalOpen,setModalOpen] = useState(false);
        const openModal=()=>{
          setModalOpen(true);
        }
        const closeModal=()=>{
          setModalOpen(false);
        }
        const [content,setContent] = useState([]);
        
        return(
            <Container id="PortFolio">
                <Header>
                    <BannerBox>
                        <BannerImg/>
                        <HeaderTitle>PROJECTS
                            <div></div>
                            <p>지금까지 작업한 프로젝트들입니다.</p> <p>열심히 추가해나갈 예정이에요 :)</p>
                        </HeaderTitle>
                    </BannerBox>
                </Header>
    
                <RecentProject>
                    <h2>최근 프로젝트</h2>
                    <div>
                        <img style={{width:"60rem", height:"40rem", margin:"0 5rem 5rem 0"}} src={pro1} alt="최근 작업"/>
                        <TextBox>
                            <Title><Empha>CTA Celeb's Tweet Analysis</Empha></Title>
                            <Text><p>✔ 트윗 내용을 분석하여 긍정/부정/중립 3가지 감성으로 나누어 보여줍니다.</p>
                            <p>✔ 자주 사용하는 단어들은 워드 클라우드로 시각화하여 제공합니다.</p> </Text>
                            <MidTitle>프로젝트 제작 기간</MidTitle>
                            <Text> 2020.09.07 - 2020.12.04</Text>
                            <MidTitle>프로젝트 기여</MidTitle>
                            <Text>웹서비스 개발(100%), 분석 모델 개발 참여(30%)</Text>
                            <MidTitle>자세한 내용</MidTitle>
                            <Text>                         CTA는 모델의 희소 행렬 연산 부분에 기존의 CSR 방식이 아닌 NZB 인덱싱 기술을 적용하여<br />
                            기존 모델보다 속도 및 메모리 접근 효율 측면에서 향상된 성능을 보여줍니다.<br/>https://url.kr/qost8b (유튜브 링크)</Text>
                        </TextBox>
                    </div>
                </RecentProject>
                <Others>
                    <Grid>
                        <div>
                            <GridBox>
                                <Circle/>
                                <LineText>WEB</LineText>
                            </GridBox>
                        </div>
                        <div>
                            <GridBox onClick={openModal}>
                                <img style={{width:"20rem", height:"20rem"}} src="https://source.unsplash.com/user/erondu/250x250" alt="기타 작업1"/>
                                <HashTag>#학식소개 #MySQL #node.js</HashTag>
                            </GridBox>
                        </div>
                        <div>
                            <VerticalText>
                                <TextBase size="1.6" bold>
                                    다양한 분야에<br/> &nbsp;&nbsp;&nbsp;&nbsp;도전하고 있어요!
                                </TextBase>
                            </VerticalText>
                        </div>
                        <div>
                        <GridBox onClick={openModal}>
                                <img style={{width:"20rem", height:"20rem"}} src="https://source.unsplash.com/user/erondu/250x250" alt="기타 작업1"/>
                                <HashTag>#자격증관리 #크롤링</HashTag>
                            </GridBox>
                        </div>
                        <div>
                        <GridBox onClick={openModal}>
                                <img style={{width:"20rem", height:"20rem"}} src="https://source.unsplash.com/user/erondu/250x250" alt="기타 작업1"/>
                                <HashTag>#영화소개 #React</HashTag>
                            </GridBox>
                        </div>
                        <div>
                            <GridBox>
                                <Circle/>
                                <LineText>APP</LineText>
                            </GridBox>
                        </div>
                        <div>
                            <GridBox onClick={openModal}>
                                <img style={{width:"20rem", height:"20rem"}} src="https://source.unsplash.com/user/erondu/250x250" alt="기타 작업1"/>
                                <HashTag>#캐릭터디자인 #마스코트제작</HashTag>
                            </GridBox>
                        </div>
                        <div>
                            <GridBox>
                                <Circle/>
                                <LineText>DESIGN</LineText>
                            </GridBox>
                        </div>

                    </Grid>
                </Others>
                {/* <button onClick={openModal}>모달팝업 {modalOpen?"true":"false"}</button> */}
                <Modal open={modalOpen} close={closeModal} header="Modal Heading">
                {/* prop.children */}
                    관련 내용을 수정하고 있어요<br/>
                    잠시만 기다려주세요!
                    <img style={{width:"20rem", height:"20rem", margin:"0 auto"}}src={construction} alt="최근 작업"/>
                </Modal>
            </Container>
        );
    };



    

// export default Project;
export {
    Project,
    Thumbnail,
    Circle
};