import React from "react";
import styled,{css} from "styled-components";

const ColumnWrap = css`
    display:flex;
    flex-direction:column;
    align-items:center;
    width:fit-content;
    padding: 0 1rem;
`
const Text=styled.span`
    font-size:${props=>props.size||1}rem;
    ${props=>{
        if(props.bold){return css`font-weight:bold;`;}
        else if(props.light){return css`font-weight:lighter`;}
    }};
    color:${props=>props.color};
`;
const TextCss=css`
    font-size:${props=>props.size||1}rem;
    ${props=>{
        if(props.bold){return css`font-weight:bold;`;}
        else if(props.light){return css`font-weight:lighter`;}
    }}
`;

const ContainerCSS = css`
//   margin: 0 15rem;
  height: 100%;
  box-sizing:border-box;
  clear:both;
  padding-top:5%; /*네비게이션 높이*/
`;

export {
    ColumnWrap,
    Text,
    TextCss,
    ContainerCSS
}
