import React from "react";
import styled,{css} from "styled-components";
import {Text,ContainerCSS,ColumnWrap} from "./Utils";
import myBox from "../image/box.svg";
import question from "../image/question.svg";
import gmail from "../image/email.svg";

const Container=styled.div`
// border:1px solid red;
    ${ContainerCSS};
    margin: 0 15rem;
    position:relative;
`;
const Wrap=styled.div`
    // border:3px solid black;
    text-align:center;
    display:grid;
    grid-template-columns: 100%; /* 각 행(세로줄)의 길이 */
    grid-template-rows: 22% 22% 21% 35%; /* 각 열(가로줄)의 길이 */
    
    position:absolute;
    bottom:0;
    left:0;
    right:0;
    height:90%;
`;
const Box=styled.div`
// border:3px solid orange;
    width:100%;

    height:100%;
    display:flex;
    flex-direction:column;
    align-items:center;
    line-height:5rem;
    
    & >div{
        margin-top:2rem;
        // border:1px solid green;
    }
`;
const TextDiv=Text.withComponent("div");
const LinkStyle={
    textDecoration:"none",
    color:"unset"
}

const BottomBar=styled.div`
// border:1px solid purple;
    display:flex;
    justify-content:space-between;

    // Box 엘리먼트 바닥에 고정
    position:absolute;
    width:100%; //이거 필수 미설정시 넓이값이 없어서 justify-content가 안된 것처럼 보임
    bottom:3rem;


    & >div:nth-child(2){
        // border:1px solid blue;
        margin-left: 12rem;
        display:flex;
    }
`;

const Highlight=styled.span`
    background:#fee4a6;
    height:100%;
`;
const LastPage=()=>{
    return(
        <Container>
            <Wrap>
                <Box>
                    <img src={question} style={{width:"12rem", height:"12rem"}} alt="question log" />
                    <TextDiv size="2.2" bold>제게 궁금하신 점이 있나요?</TextDiv>
                </Box>
                <Box style={{transform:"translateY(3rem)"}}>
                    <img src={gmail} style={{width:"5.2rem",height:"5.2rem",marginTop:"2.2rem"}}alt="gmail logo"/>
                    <TextDiv size="4.2" bold ><Highlight>&nbsp;uknowsj@gmail.com&nbsp;</Highlight></TextDiv> 
                </Box>
                <Box>
                <TextDiv size="2" color="#4c4c4c" style={{marginTop:"6rem"}} light>
                    늦어도 이틀 내에는 답장을 보내드릴게요 :)<br/>
                    <Text size="2" color="#4c4c4c" bold>읽어주셔서 감사합니다!</Text> 
                </TextDiv>
                </Box>
                <Box style={{lineHeight:"unset"}}>
                    <BottomBar>
                        <TextDiv size="2" bold>
                            <a style={LinkStyle} href="https://github.com/uknowsj" target="_blank">GitHub &nbsp;&nbsp;</a> 
                            <a style={LinkStyle} href="#" target="_blank">Blog</a>
                        </TextDiv>
                        <div >
                            <img src={myBox} style={{width:"3rem",height:"3rem",marginRight:"3rem"}}alt="logo"/>    
                            <TextDiv size="2" bold>Made By uknowsj</TextDiv>
                        </div>
                    </BottomBar>
                </Box>
            </Wrap>
        </Container>

    );
}

export default LastPage;