import React from "react";
import styled, {css,keyframes} from "styled-components";
import {TextCss} from "./Utils";

// 페이지 전체 감싸줄 컨테이너
const Container = styled.div`
  margin: 10rem 15rem 20rem 15rem;
  height: auto;
//   border: 2px solid blue;
  box-sizeing:border-box;
  display:block;
  @media (max-width: 639px) {
      margin:10rem 5rem 20rem 5rem; 
  }
`;

// 중앙정렬 타이틀
const AlignCenter = styled.h2`
    text-align:center;
    font-size:4.0rem;
    // border:1px solid black;
    margin-bottom:5rem;
`;

//container
const SkillContainer = styled.div`
    display:flex;
    flex-direction:column;
    height:auto;
    width:60%;
    // border:2px solid green;
    float:${props=>props.direction || "left"};
    margin-top:5rem;
    padding-top:5%; /*네비게이션 높이*/
    `;

//컨테이너 내 구분선
const Line = styled.div`
    border-top:8px solid #ffaaa6;
    boder-right:none;
    boder-left:none;
    border-bottom:3px solid rgba(255,170,166,0.6);
    height:0.8rem;
    margin:3rem 0;
`;

// Skill Field
// flex item => position absolute+transform보다 textAlgin+padding
const SkillTitle = styled.div`
    font-size:3.5rem;
    font-weight:bold;
    // border:1px solid black;
    text-align:${props=>props.direction}; 
    ${props=>{
        if(props.direction==="left"){
            return css`padding-left:5rem`;
        }return css`padding-right:5rem`;
    }}
`;

//Skill Content
const List = styled.div`
    list-style:none;
    // border:1px solid black;
    font-size:2.5rem;
    font-weight:bold;
    line-height:6rem;
`;
//그래프 막대 
const Graph = styled.div`
    width:8rem;
    height:2rem;
    background-color:#fee4a6;
    border:3px solid rgba(57,57,61,0.8);
    border-radius:5%;
    margin:0;
`;
// Nesting 부모->자식(그래프막대) 속성 전달
const GraphContainer =styled.div`
    display:flex;
    align-items:center;
    & li{ //자기자신 선택가능 &
      min-width:20rem;
    //   border:1px solid red;
    }
    ${Graph}{ //그래프속성변경
        width:${props=>props.percent*=8}rem;
        margin-${props=>props.direction}:2rem;
    }
`;

// 위에 float:right/left 떄문에 ..
const Clear = styled.div`
    clear:both;
`; 

//세로글씨 
//span이라 div로 따로 작성
const Text=styled.div`
    ${TextCss}
`;
const VerticalText=styled.div`
    writing-mode:vertical-lr;
    position:relative;
    transform:translate(20rem, 15rem);
    
    & >div:nth-child(2){
        transform:translate(2rem, 2rem);
    }
`;

const Title = styled.h1`
    font-size: 150px;
    color:${props=>props.color || "black"};
    &:hover{
        color:red;
    }
    &+h1{
        margin-top:20px;
    }
`;

const Skill=()=>{
    const shadowStyle={
        color:"#f9f2f2",
        textShadow: "1.5px 0px 1px rgba(57, 57, 61, 0.3), 0px 1.5px 1px rgba(57, 57, 61, 0.3), -1.5px 0px 1px rgba(57, 57, 61, 0.3),0px -1.5px 1px rgba(57, 57, 61, 0.3)",
      webkitTextSizeAdjust: "100%",
    }

    return(
        <Container id="Skill">
                <AlignCenter>저는 이런 것들을 할 줄 알아요</AlignCenter>

                <SkillContainer>
                    <SkillTitle direction="left">
                        FRONT-END
                    </SkillTitle>
                        <Line/>
                    <List>
                        <GraphContainer percent="6" direction="left">
                            <li>HTML/CSS</li>
                            <Graph/>
                            <p>&nbsp;&nbsp;60%</p>
                        </GraphContainer>
                        <GraphContainer percent="7" direction="left">
                            <li>JavaScript</li>
                            <Graph/>
                            <p>&nbsp;&nbsp;70%</p>
                        </GraphContainer>
                        <GraphContainer percent="5" direction="left">
                            <li>React</li>
                            <Graph/>
                            <p>&nbsp;&nbsp;50%</p>
                        </GraphContainer>

                    </List>
                </SkillContainer>
                <VerticalText>
                    <Text size="2.2" bold style={shadowStyle}>요즘에는 React,CSS를</Text>
                    <Text size="2.2" bold style={shadowStyle}>열심히 배우고 있어요</Text>
                </VerticalText>
                {/* BACK-END */}
                <SkillContainer direction="right">
                    <SkillTitle direction="right">
                        BACK-END
                    </SkillTitle>
                        <Line/>
                    <List>
                        <GraphContainer percent="7" direction="right">
                            <p>70%&nbsp;&nbsp;</p>
                            <Graph/>
                            <li>Node.js</li>
                        </GraphContainer>
                        <GraphContainer percent="6" direction="right">
                            <p>60%&nbsp;&nbsp;</p>
                            <Graph/>
                            <li>Python/Flask</li>
                        </GraphContainer>
                        <GraphContainer percent="4" direction="right">
                            <p>40%&nbsp;&nbsp;</p>
                            <Graph/>
                            <li>Java/Spring..</li>
                        </GraphContainer>
                        <GraphContainer percent="6" direction="right">
                            <p>60%&nbsp;&nbsp;</p>
                            <Graph/>
                            <li>MongoDB/MySQL..</li>
                        </GraphContainer>
                    </List>
                </SkillContainer>     

                {/* Others */}
                <SkillContainer direction="left">
                    <SkillTitle direction="left">
                        Others
                    </SkillTitle>
                        <Line/>
                    <List>
                        <GraphContainer percent="7" direction="left">
                            <li>Adobe Illustrator</li>
                            <Graph/>
                            <p>&nbsp;&nbsp;70%</p>
                        </GraphContainer>
                        <GraphContainer percent="5" direction="left">
                            <li>Adobe Premiere</li>
                            <Graph/>
                            <p>&nbsp;&nbsp;50%</p>
                        </GraphContainer>
                    </List>
                </SkillContainer>    
                <Clear />
        </Container>
    );
}
export default Skill;