import React, { useEffect, useState } from "react";

import "./Main.css"
// import Navigate from "./Navigate";
import orange from "../image/orange.svg";
import peach from "../image/peach.svg";
import lines from "../image/lines.svg";
import card from "../image/card.svg";
import styled from "styled-components";


const Main = () => {
  // 현재 스크롤 위치를 저장하는 state 
  // 0으로 초기화하여 사용
  // 값은 y, 변경은 seyY를 사용
  const [y, setY] = useState(0)


  // 아래 useEffect 와 세트
  // 현재 스크롤 값을 event 에서 가져와 y 에 저장한다
  const handleScroll = (e) => {
    const scrollTop = ('scroll', e.srcElement.scrollingElement.scrollTop);
    setY(scrollTop)
  }

  // 스크롤 변화를 감지하여 handleScroll 를 동작
  // componentDidMount, componentDidUpdate 의 역활을 한다
  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    // clean up 함수
    // componentDidUnMount 의 역활을 한다
    return (() => {
      window.removeEventListener('scroll', handleScroll);
    })
  }, [])

  // componentDidUpdate 의 역활을 한다
  useEffect(() => {
    // id 가 매인인 태그에 접근한다
    // 아래 main 을 console 에 찍으면
    // <div className="page MainPost" data-aos="fade-down" onScroll={handleScroll} id='main'></div> 가 나온다
    const main = document.getElementById('main')

    // id 가 main 태그의 높이값을 가져온다
    const objHeight = main.getBoundingClientRect().height;

    if (y > objHeight) return
    // main 의 높이와 현재 스크롤 위치를 나누어서 현재 위치를 퍼센티지화 한 값을 투명도에 적용
    // -800 한 이유는 좀 더 일찍 사라지기 위함 / 늦게 사라지면 다음에 나오는 콘텐츠와 겹치는 이슈로 가시성이 떨어짐
    main.style.opacity = 1 - (y / (objHeight - 80))
    // 의존성 배열
    // y의 값이 변경될 때 마다 39 ~ 42 번째 줄이 동작한다
  }, [y])


  return (
    // before
    // <div className="page MainPost">
    // <Div className="MainPost" data-aos="fade-up" data-aos-anchor-placement="top-bottom" onScroll={handleScroll} id='main'>
    <Div className="MainPost" data-aos-anchor-placement="top-bottom" onScroll={handleScroll} id='main'>
      <div className="main-items">
        <div className="container">
          <div className="movingDots">
            <img src={orange} alt="orangeDot" />
            <img src={peach} alt="peachDot" />
            <img src={lines} alt="lines" />
          </div>
          <div className="greeting" >
            <h1>안녕하세요 - !</h1>
            <h2>웹개발자를 꿈꾸는 <span className="pink"> 양승주</span> 입니다.</h2>
            <h4>만나서 반갑습니다 :)</h4>
          </div>
            <img id="card" src={card} alt="PhotoCard" />
        </div>
      </div>
    </Div>

  );
}

const Div = styled.div`
`

export default Main;